<template>
  <div class="forgetPassword">
    <Form ref="formCustom" :model="formData" :rules="rule" :label-width="140">
      <FormItem label="邮箱" prop="account">
        <Input type="text" v-model="formData.account" placeholder="请输入注册邮箱"/>
      </FormItem>
      <FormItem prop="verificationCode" class="verificationCode">
        <Row>
          <Col span="8">
            <Input type="text" v-model="formData.verificationCode" />
          </Col>
          <Col span="14">
            <Button v-if="!hasSencode" @click="getVerificationCode" class="button">获取验证码</Button>
            <Button v-else class="button countDown">{{countDown}}s后再获取</Button>
          </Col>
        </Row>
      </FormItem>
      <FormItem label="新密码" prop="password">
        <Input type="password" v-model="formData.password" />
      </FormItem>
      <FormItem label="确认新密码" prop="confirmPassword">
        <Input type="password" v-model="formData.confirmPassword" />
      </FormItem>
      <FormItem>
        <Button class="loginbtn" @click="handleReset('formCustom')">
          修改密码
        </Button>
      </FormItem>
      <FormItem class="forget">
        <span @click="gotoRegister">注册</span>
        <span @click="gotoLogin" class="register">登录</span>
      </FormItem>
    </Form>
  </div>
</template>

<script>
import { sendCode, findPassword } from "@/api/user.js";
export default {
  name: "ForgetPassword",
  data() {
    const validatePassWord = (rule, value, callback) => {
      const passWord = /^.{6,}$/;
      if (value === "") {
        callback(new Error("密码不能为空"));
      } else if (!passWord.test(value)) {
        callback(new Error("请输入6位以上的密码"));
      } else {
        callback();
      }
    };
    const validatePassCheck = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.formData.password) {
        callback(new Error("两次密码不一样"));
      } else {
        callback();
      }
    };
    return {
      formData: {
        account: "",
        verificationCode: "",
        password: "",
        confirmPassword: ""
      },
      rule: {
        account: [{ required: true, message: "账号不能为空", trigger: "blur" }],
        verificationCode: [{ required: true, message: "验证码不能为空", trigger: "blur" }],
        password: [{ validator: validatePassWord, required: true, trigger: "blur" }],
        confirmPassword: [{ validator: validatePassCheck, required: true, trigger: "blur" }]
      },
      hasSencode: false,
      countDown: 60
    };
  },
  methods: {
    getVerificationCode() {
      if (!this.formData.account) {
        this.$Message.error("请输入邮箱或手机");
        return false;
      }
      const params = {
        account: this.formData.account
      };
      sendCode(params)
        .then(res => {
          this.hasSencode = true;
          this.$Message.success("发送成功");
          this.theCountdown();
        });
    },
    theCountdown() {
      const minute = setInterval(res => {
        this.countDown--;
        if (this.countDown === 0) {
          clearInterval(minute);
          this.countDown = 60;
          this.hasSencode = false;
        }
      }, 1000);
    },
    // 修改密码
    handleReset(name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          const params = {
            account: this.formData.account,
            pwd: this.formData.password,
            repwd: this.formData.confirmPassword,
            code: this.formData.verificationCode
          };
          findPassword(params).then(res => {
            this.$Message.success("修改成功");
          });
        } else {
          this.$Message.warning("请完整输入必填信息");
        }
      });
    },
    gotoRegister() {
      this.$router.replace({
        path: "/register"
      });
    },
    gotoLogin() {
      this.$router.replace({
        path: "/login"
      });
    }
  }
};
</script>

<style lang="less" scoped>
  .verificationCode {
    .ivu-row {
      height: 100%;
      width: 100%;
      justify-content: space-between;
      .ivu-col {
        /deep/.ivu-input-wrapper {
          width: 100%;
        }
      }
      .button {
        width: 100%;
        height: 100%;
        background: #15121F;
        border-color: @warn;
        color: @warn;
        font-size: 18px;
      }
      .countDown {
        cursor: no-drop;
        border-color: #e0e0e0;
        color: #e0e0e0;
      }
    }
  }
  .loginbtn {
    background: @warn;
    border-color: @warn;
    color: #fff;
    width: 100%;
    height: 100%;
    &:hover {
      color: #fff;
    }
    /deep/span {
      font-size: 18px;
    }
  }
  .forget {
    /deep/.ivu-form-item-content {
      color: #e0e0e0;
      cursor: pointer;
      display: flex;
      justify-content: space-between;
      font-size: 18px;
      a {
        color: #e0e0e0;
      }
      .register {
        color: @warn;
        letter-spacing: 8px;
      }
    }
  }
</style>
